:root{
    --bg-button: #f0f0f0;
    --border-button: solid 1px #888;

}

.button{
    font-size: 1.4em;
    background-color: var(--bg-button);
    border: none;
    border-right: var(--border-button);
    border-bottom: var(--border-button);
    outline: none;
    margin: 5px;
    border-radius: 5px;
    box-shadow: 14px 16px 17px -15px rgba(0,0,0,0.75);
    
}


.button:active{
    background-color: #ccc;
}

.button.double{
    grid-column: span 2;
}

.button.triple{
    grid-column: span 3;
}

.button.operation{
    background-color: #727272;
    color: #fff;
}

.button.operation:active{
    background-color: #5f5f5fcc;
}